
.section02 .s2_wrap {
  position: relative;
  display: inline-block;
  width: 769px;
  height: 440px;
}

.section02 .s2_wrap * {
  position: absolute;
  display: inline-block;
}

.section02 .s2_wrap {
  display: block;
  margin-top: 20px;
  text-align: center;
}

.section02 .s2_wrap>span {
  display: inline-block;
  margin: 0 10px;
  width: 126px;
  height: 126px;
  background: #1e1d22;
  border-radius: 50%;
  cursor: pointer;
}

.section02 .s2_wrap>i {
  display: inline-block;
  margin: 50px 75px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #000;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.35);
}

.section02 .s2_wrap .btn_A {
  top: 86px;
  left: 0;
  background: #000;
}

.section02 .s2_wrap .btn_B {
  top: 25px;
  left: 150px;
  background: #000;
}

.section02 .s2_wrap .btn_C {
  top: 0;
  left: 310px;
  background: #000;
}

.section02 .s2_wrap .btn_D {
  top: 25px;
  right: 150px;
  background: #000;
}

.section02 .s2_wrap .btn_E {
  top: 86px;
  right: 0;
  background: #000;
}

.button_A {
  top: 300px;
  left: 200px;
}

.button_B {
  top: 300px;
  left: 250px;
}

.button_C {
  top: 300px;
  left: 300px;
}

.button_D {
  top: 300px;
  left: 350px;
}

.button_E {
  top: 300px;
  left: 400px;
}

.section02 .s2_wrap .btn_A:hover,
.section02 .s2_wrap .btn_A.on {
  background-color: #404d7f;
}

.section02 .s2_wrap .btn_B:hover,
.section02 .s2_wrap .btn_B.on {
  background-color: #65407f;
}

.section02 .s2_wrap .btn_C:hover,
.section02 .s2_wrap .btn_C.on {
  background-color: #7f4064;
}

.section02 .s2_wrap .btn_D:hover,
.section02 .s2_wrap .btn_D.on {
  background-color: #FF4D44;
}

.section02 .s2_wrap .btn_E:hover,
.section02 .s2_wrap .btn_E.on {
  background-color: #7f5e40;
}


.section02 .content_box>div {
  margin-left: 130px;
  width: 500px;
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.section02 .content_box .con_A {
  background-color: #404d7f;
}

.section02 .content_box .con_B {
  background-color: #65407f;
}

.section02 .content_box .con_C {
  background-color: #7f4064;
}

.section02 .content_box .con_D {
  background-color: #FF4D44;
}

.section02 .content_box .con_E {
  background-color: #7f5e40;
}

.section02 .s2_wrap>i.on,
.section02 .s2_wrap>i.hover {
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

.section02 .s2_wrap>i.on:after,
.section02 .s2_wrap>i.hover:after {
  content: '';
  display: inline-block;
  position: absolute;
  top: -1px;
  left: -1px;
  border-radius: 50px;
  width: 16px;
  height: 16px;
  -webkit-box-shadow: 0;
  -moz-box-shadow: 0;
  box-shadow: 0;
}

.section02 .s2_wrap>i.hover:before,
.section02 .s2_wrap>i.on.ani:before {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.5);
  transform-origin: center;
  width: 32px;
  height: 32px;
  border-radius: 50px;
  animation: circle 1.3s linear infinite;
  opacity: 1;
  box-sizing: border-box;
}

.button_A.hover.ani:before,
.button_A.on.ani:before {
  border: 2px solid #2c72ff;
}

.button_B.hover.ani:before,
.button_B.on.ani:before {
  border: 2px solid #b449ff;
}

.button_C.hover.ani:before,
.button_C.on.ani:before {
  border: 2px solid #ff4992;
}

.button_D.hover.ani:before,
.button_D.on.ani:before {
  border: 2px solid #ff4d44;
}

.button_E.hover.ani:before,
.button_E.on.ani:before {
  border: 2px solid #ff8a44;
}

.section02 .s2_wrap .button_A:after {
  background-color: #404d7f;
}

.section02 .s2_wrap .button_B:after {
  background-color: #65407f;
}

.section02 .s2_wrap .button_C:after {
  background-color: #7f4064;
}

.section02 .s2_wrap .button_D:after {
  background-color: #FF4D44;
}

.section02 .s2_wrap .button_E:after {
  background-color: #7f5e40;
}


@keyframes circle {
  0% {
    transform: translate(-50%, -50%) scale(0.5);
    opacity: 1;
  }

  100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 0;
  }
}

.content {
  display: none;
}

.content.con_A {
  display: block;
}
